<template>
  <v-container>
    <v-row justify="center">
      <v-card width="100%">
        <v-app-bar text dense color="transparent" elevation="0">
          <v-toolbar-title>
            <h4>List Project</h4>
          </v-toolbar-title>
          <v-spacer />
          <v-btn color="primary" @click="handleNewProject()">New</v-btn>
        </v-app-bar>
        <v-divider></v-divider>
        <v-card-text class="pa-0">
          <v-list two-line class="pa-0">
            <template v-for="(item, index) in projects">
              <v-subheader v-if="item.header" :key="item.header">{{ item.header }}</v-subheader>
              <v-divider v-else-if="item.divider" :key="index"></v-divider>
              <v-list-item v-else :key="item.title" @click="handleClick(item.id)">
                <v-list-item-avatar :color="item.color">
                  <v-icon dark>{{ item.icon }}</v-icon>
                </v-list-item-avatar>
                <v-list-item-content>
                  <v-list-item-subtitle v-html="item.name"></v-list-item-subtitle>
                </v-list-item-content>
                <v-list-item-action class="caption">{{ item.timeLabel }}</v-list-item-action>
              </v-list-item>
            </template>
          </v-list>
        </v-card-text>
      </v-card>
    </v-row>
  </v-container>
</template>

<script>
import notes from "@/api/notification";
import { db } from "../firebaseConfig.js";

export default {
  data() {
    return {
      items: notes,
      projects: []
    };
  },
  created() {
    this.$store.commit("setMenu", [
      { header: "Apps" },
      {
        text: "akacon",
        icon: "dashboard",
        to: "/dashboard"
      }
    ]);
    this.getProjects();
  },
  methods: {
    handleClick(id) {
      this.$router.push(`/${id}/function/list`);
    },
    handleNewProject() {
      this.$router.push("/newproject");
    },
    getProjects() {
      db.ref("/projects/" + this.$store.state.currentUser.uid)
        .once("value")
        .then(snap => {
          snap.forEach(doc => {
            var project = {};
            project.name = doc.val().name;
            project.id = doc.key;
            this.projects.push(project);
          });
        });
    }
  }
};
</script>
